<template>
  <div class="field-opt pageSpace h100">
    <my-breadcrumb :extra="extraBreadcrumb"></my-breadcrumb>
    <div class="pageContent">
      <div class="optContent" style="margin:20px 0">
        <a-form-model ref="fieldForm" :model="fieldForm" :rules="fieldFormRule" layout="horizontal">
          <p class="partTitle">字段基本信息</p>
          <a-row :gutter="[24,24]" style="margin:0">
            <a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
              <a-form-model-item
                label="字段中文"
                prop="cname"
                has-feedback
                :labelCol="{span:4}"
                :wrapperCol="{span:18}"
              >
                <a-input v-model="fieldForm.cname" placeholder="请输入字段名" />
              </a-form-model-item>
            </a-col>
            <a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
              <a-form-model-item
                label="字段英文"
                prop="ename"
                :labelCol="{span:4}"
                :wrapperCol="{span:18}"
              >
                <a-input v-model="fieldForm.ename" placeholder="请输入字段英文名" />
              </a-form-model-item>
            </a-col>
            <a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
              <a-form-model-item
                label="字段类型"
                prop="type"
                :labelCol="{span:4}"
                :wrapperCol="{span:18}"
              >
                <a-select v-model="fieldForm.type" placeholder="请选择字段数据类型">
                  <a-select-option value="string">字符串类型</a-select-option>
                </a-select>
              </a-form-model-item>
            </a-col>
            <a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
              <a-form-model-item
                label="组件类型"
                prop="componentType"
                :labelCol="{span:4}"
                :wrapperCol="{span:18}"
              >
                <a-select
                  v-model="fieldForm.componentType"
                  placeholder="请选择组件类型"
                  @change="componentTypeChange"
                >
                  <a-select-option value="input">输入框</a-select-option>
                  <a-select-option value="select">下拉单选框</a-select-option>
                  <a-select-option value="m-select">下拉多选框</a-select-option>
                  <!-- <a-select-option value="textarea">文本框</a-select-option> -->
                  <!-- <a-select-option value="num">数字输入框</a-select-option> -->
                  <!-- <a-select-option value="time">时间范围选择框</a-select-option> -->
                  <a-select-option value="day">日期范围选择框</a-select-option>
                  <a-select-option value="daterange">日期时间范围选择框</a-select-option>
                </a-select>
              </a-form-model-item>
            </a-col>
            <!--<a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
              <a-form-model-item
                label="检索方式"
                prop="searchType"
                :labelCol="{span:4}"
                :wrapperCol="{span:18}"
              >
                <a-radio-group v-model="fieldForm.searchType">
                  <a-radio value="term">精确</a-radio>
                  <a-radio
                    value="fuzzy"
                    :disabled="fieldForm.componentType === 'date' || fieldForm.componentType === 'daterange'"
                  >模糊</a-radio>
                </a-radio-group>
              </a-form-model-item>
            </a-col>-->

            <a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
              <a-form-model-item
                label="精确检索"
                prop="searchType"
                :labelCol="{span:4}"
                :wrapperCol="{span:18}"
              >
                <a-switch v-model="fieldForm.searchType"></a-switch>
              </a-form-model-item>
            </a-col>

            <a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
              <a-form-model-item
                label="是否检索"
                prop="isSearch"
                :labelCol="{span:4}"
                :wrapperCol="{span:18}"
              >
                <a-switch v-model="fieldForm.isSearch"></a-switch>
              </a-form-model-item>
            </a-col>
            <!-- <a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
              <a-form-model-item label="是否统计" prop="isStatistics" :labelCol="{span:4}" :wrapperCol="{span:18}">
                <a-switch v-model="fieldForm.isStatistics"></a-switch>
              </a-form-model-item>
            </a-col>-->
            <a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
              <a-form-model-item
                label="是否启用"
                prop="isEnabled"
                :labelCol="{span:4}"
                :wrapperCol="{span:18}"
              >
                <a-switch v-model="fieldForm.isEnabled"></a-switch>
              </a-form-model-item>
            </a-col>
            <!-- <a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
              <a-form-model-item label="是否必填" prop="isWrite" :labelCol="{span:4}" :wrapperCol="{span:18}">
                <a-switch v-model="fieldForm.isWrite"></a-switch>
              </a-form-model-item>
            </a-col>-->
            <a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
              <a-form-model-item
                label="校验规则"
                prop="rules"
                :labelCol="{span:4}"
                :wrapperCol="{span:18}"
              >
                <a-select v-model="fieldForm.rules" placeholder="请选择校验规则">
                  <a-select-option value="no">无规则</a-select-option>
                  <a-select-option value="ip">IP规则</a-select-option>
                  <a-select-option value="email">邮箱规则</a-select-option>
                </a-select>
              </a-form-model-item>
            </a-col>
            <a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
              <a-form-model-item
                label="支持多值"
                prop="isMoreValue"
                :labelCol="{span:4}"
                :wrapperCol="{span:18}"
              >
                <a-switch
                  v-model="fieldForm.isMoreValue"
                  :disabled="fieldForm.componentType === 'm-select'||fieldForm.componentType === 'select'||fieldForm.componentType === 'num'||fieldForm.componentType === 'date'||fieldForm.componentType === 'daterange'"
                ></a-switch>
              </a-form-model-item>
            </a-col>
          </a-row>
          <p class="partTitle">字段组件配置</p>
          <a-row :gutter="[24,24]" style="margin:0">
            <a-col :xxl="16" :xl="24" style="padding: 0 12px 12px;">
              <a-form-model-item
                label="默认值"
                prop="defaultValue"
                :labelCol="{span:2}"
                :wrapperCol="{span:22}"
              >
                <div v-if="fieldForm.componentType === 'date'">
                  <a-select v-model="fieldForm.defaultValue" placeholder="请选择时间点">
                    <a-select-option value="">手动选择时间</a-select-option>
                    <a-select-option value="systemTime">当前系统时间</a-select-option>
                  </a-select>
                </div>
                <div v-else-if="fieldForm.componentType === 'daterange'">
                  <a-select v-model="fieldForm.defaultValue" placeholder="请选择时间范围">
                    <a-select-option value="">手动选择</a-select-option>
                    <a-select-option value="today">今天</a-select-option>
                    <a-select-option value="threeDays">近三天</a-select-option>
                    <a-select-option value="sevenDays">近七天</a-select-option>
                    <a-select-option value="thirtyDays">近三十天</a-select-option>
                    <a-select-option value="threeMonth">近三月</a-select-option>
                    <a-select-option value="halfYear">近半年</a-select-option>
                    <a-select-option value="year">今年</a-select-option>
                  </a-select>
                </div>
                <div v-else>
                  <a-input v-model="fieldForm.defaultValue" placeholder="请输入默认显示的值，如果支持多值，以英文逗号分隔" />
                </div>
              </a-form-model-item>
            </a-col>
          </a-row>
          <a-row :gutter="[24,24]" style="margin:0">
            <a-col :xxl="16" :xl="24" style="padding: 0 12px 12px;">
              <a-form-model-item
                label="前置筛选"
                prop="isFrontComponent"
                :labelCol="{span:2}"
                :wrapperCol="{span:22}"
              >
                <a-switch v-model="fieldForm.isFrontComponent"></a-switch>
              </a-form-model-item>
            </a-col>
          </a-row>
          <a-card
            class="bt"
            title="下拉框组件配置"
            v-if="fieldForm.componentType === 'select' || fieldForm.componentType === 'm-select'"
          >
            <a-row :gutter="[24,24]" style="margin:0">
              <a-col :xxl="16" :xl="24" style="padding: 0 12px 12px;">
                <a-form-model-item
                  label="数据来源"
                  prop="selectComponentDataType"
                  :labelCol="{span:2}"
                  :wrapperCol="{span:22}"
                >
                  <a-radio-group v-model="fieldForm.selectComponentDataType">
                    <a-radio :value="1">手动输入</a-radio>
                    <a-radio :value="2">接口api</a-radio>
                  </a-radio-group>
                </a-form-model-item>
              </a-col>
              <a-col :xxl="16" :xl="24" style="padding: 0 12px 12px;">
                <a-form-model-item
                  label="数据配置"
                  prop="options"
                  :labelCol="{span:2}"
                  :wrapperCol="{span:22}"
                >
                  <div v-if="fieldForm.selectComponentDataType === 1">
                    <div v-for="(value,index) of selectOptions.values" :key="index">
                      <a-input
                        v-model="selectOptions.values[index]"
                        placeholder="请输入下拉框key"
                        style="width:40%;margin-right:1%"
                      />
                      <span style="color:#fff;">~</span>
                      <a-input
                        v-model="selectOptions.labels[index]"
                        placeholder="请输入下拉框value"
                        style="width:40%;margin:0 1%"
                      />
                      <a-button
                        type="default"
                        shape="circle"
                        @click="delOption('selectOptions',index)"
                      >
                        <a-icon type="minus"></a-icon>
                      </a-button>
                    </div>
                    <a-button type="primary" @click="addOption('selectOptions')">
                      <a-icon type="plus"></a-icon>添加
                    </a-button>
                  </div>
                  <div v-else>
                    <a-select v-model="fieldForm.options" placeholder="选择相关接口">
                      <a-select-option :value="inter.name" v-for="inter of interfaceData" :key="inter.id">{{inter.name}}</a-select-option>
                    </a-select>
                  </div>
                </a-form-model-item>
              </a-col>
            </a-row>
          </a-card>
        </a-form-model>
        <a-row :gutter="[24,24]">
          <a-col :xxl="24" :xl="24" class="ar action">
            <a-button type="primary" @click="save">
              <a-icon type="save"></a-icon>保存
            </a-button>
            <a-button type="default" @click="back">
              <a-icon type="rollback"></a-icon>返回
            </a-button>
          </a-col>
        </a-row>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "field-opt",
  data() {
    let validateKeywords = (rule,value,callback) => {
      if(!value.trim()){
        callback(new Error('请输入英文名'));
      } else {
        if(!this.$fun.regularTop(this.fieldForm.ename)){
          callback(new Error('只能以字母开头'));
        } else if(this.$fun.inKeywords(this.fieldForm.ename)){
          callback(new Error('不能为特殊关键词'));
        } if(this.nodeFiledsEn.indexOf(this.fieldForm.ename)!=-1){
              callback(new Error('字段英文名重复，请重新填写'));
          }else {
          callback();
        }
      }
    };
    let validateNodeFileds = (rule,value,callback) => {
      if(!value.trim()){
        callback(new Error('请输入字段名'));
      } else {
          if(this.nodeFileds.indexOf(this.fieldForm.cname)!=-1){
          callback(new Error('字段名重复，请重新填写'));
        }  else {
          callback();
        }
      }
    };
    return {
      extraBreadcrumb: [
        "频道字段管理",
        this.$route.query.channelName,
        this.$route.params.type == "edit" ? "编辑" : "添加",
      ],
      fieldForm: {
        cname: "", // 中文名
        ename: "", // 英文名
        componentType: "input", // 组件类型
        type: "string", // 字段类型
        // searchType: "term", // 检索精度 fuzzy term
        searchType: true, // 检索精度 fuzzy term
        isSearch: true, // 是否检索
        // isStatistics: true, // 是否统计
        isEnabled: true, // 是否启用
        // isWrite:false, // 是否必填
        isMoreValue: false, // 是否多值
        isFrontComponent: false, // 是否开启前置
        rules: "no", // 字段规则  string
        options: "", // 下拉框字段配置  string
        defaultValue: "", // 字段默认值 string
        selectComponentDataType: 1, // 下拉组件配置选择是手动输入1 还是接口api 2
      },
      fieldFormRule: {
        cname: [
            // { required: true, message: "请输入字段名", trigger: "blur" }
            { validator: validateNodeFileds, trigger: "blur" },
            ],
        ename: [
          // { required: true, message: "请输入字段英文", trigger: "blur" },
          { validator: validateKeywords, trigger: "blur" },
        ],
        componentType: [
          { required: true, message: "请选择组件类型", trigger: "change" },
        ],
      },
      selectOptions: {
        values: [],
        labels: [],
      },
      interfaceData:[],
      nodeFileds:[],
      nodeFiledsEn:[]
    };
  },
  methods: {
    // 配置的添加删除
    addOption(option) {
      this[option].values.push("");
      this[option].labels.push("");
    },
    delOption(option, i) {
      this[option].values.splice(i, 1);
      this[option].labels.splice(i, 1);
    },
    // 组件类型切换
    componentTypeChange(type) {
      this.fieldForm.defaultValue = "";
      switch (type) {
        case "m-select":
          this.fieldForm.isMoreValue = true;
          break;
        case "select":
          this.fieldForm.isMoreValue = false;
          break;
        case "num":
          this.fieldForm.isMoreValue = false;
          break;
        case "date":
          this.fieldForm.isMoreValue = false;
          this.fieldForm.searchType = true;
          // this.fieldForm.searchType = "term";
          break;
        case "daterange":
          this.fieldForm.isMoreValue = false;
          this.fieldForm.searchType = true;
          // this.fieldForm.searchType = "term";
          break;
      }
    },
    save() {
      // 先获取这是编辑还是添加
      let optType = this.$route.params.type;
      // 获取ctype
      let ctype = this.$route.query.ctype;
        this.fieldForm.searchType = this.fieldForm.searchType?"term":"fuzzy";
        // 这里进行表单验证
      this.$refs.fieldForm.validate((valid) => {
        if (valid) {
          // 检验数据配置方面 判断下拉框的数据源类型做处理
          let data = this.$fun.bolToInt(this.fieldForm);
          let finallybol = true;
          if (
            data.componentType === "select" ||
            data.componentType === "m-select"
          ) {
            // 说明是下拉框
            if (data.selectComponentDataType === 1) {
              // 说明数据源是手动输入
              let bol = this.isEffectSelectOptions();
              finallybol = bol;
              if (bol) {
                // 校验通过 将该数据做处理
                data.options = JSON.stringify(this.setHandleOptionData());
              }
            } else {
              finallybol = data.options?true:false;
              if(!finallybol){
                this.$message.warning("请选择下拉框数据配置接口");
              }
            }
          }
          if(!finallybol) {
            return
          }
          let tempArr = ["objectId","ruleGroupId","ruleId"];
          data["ownerType"] = ctype;
          data[tempArr[ctype]] = this.$route.query.cid;

          if(optType === 'add'){
            this.addField(data);
          } else {
            this.editField(data);
          }
        } else {
          this.$message.warning("输入有误，请检查");
          return false;
        }
      });
    },
    // 对 selectOptions 的有效性进行判断
    isEffectSelectOptions() {
      let finallyBol = true;
      let bol =
        this.selectOptions.values.every((item) => item.trim().length <= 0) ||
        this.selectOptions.labels.every((item) => item.trim().length <= 0);
      let bol2 =
        Array.from(new Set(this.selectOptions.values)).length <
        this.selectOptions.values.length;
      if (bol) {
        this.$message.warning(
          "下拉框数据配置不得为空，请输入有效的key、value,"
        );
        finallyBol = false;
      } else if (bol2) {
        this.$message.warning("下拉框数据配置的key不允许重复");
        finallyBol = false;
      } else {
        finallyBol = true;
      }
      console.log(finallyBol)
      return finallyBol;
    },
    // 对下拉框手动输入数据做处理
    setHandleOptionData() {
      let arr = [];
      this.selectOptions.values.forEach((key, index) => {
        let obj = {};
        obj.value = key;
        obj.label = this.selectOptions.labels[index];
        arr.push(obj);
      });
      return arr;
    },
    // 添加字段
    addField(param){
      this.$API.addField(param).then(res => {
        if(res.code === 1000) {
          this.$message.success('添加字段成功');
          history.go(-1);
        } else {
          this.$message.warning(res.message);
          console.error(res.message);
        }
      })
    },
    // 编辑字段
    editField(param){
      this.$API.editField(param).then(res => {
        if(res.code === 1000) {
          this.$message.success('编辑字段成功');
          history.go(-1);
        } else {
          this.$message.warning('字段失败');
          console.error(res.message,res.data);
        }
      })
    },
    // 查询数据 fieldId 
    getFieldByFieldId(fieldId){
      this.$API.searchFieldById(fieldId).then(res => {
        if(res.code === 1000) {
          let data = this.$fun.intToBol(res.data);
            data.searchType = data.searchType==="term"?true:false;
          if(data.options&&data.selectComponentDataType ===1){
            // 如果options有数据
            let temp = JSON.parse(data.options);
            this.selectOptions.values = temp.map(list => list.value);
            this.selectOptions.labels = temp.map(list => list.label);
          }
          Object.assign(this.fieldForm,data);
        } else {
          this.$message.warning('获取字段失败');
          console.error(res.message,res.data);
        }
      })
    },
     // 获取当前点击节点的字段
      getDicData(){
        let id = this.$route.query.cid;
        let ownerType = this.$route.query.ctype;
        this.$API.searchDic({id,ownerType}).then(res => {
              if(res.code === 1000){
                  for(let item in res.data){
                      this.nodeFileds.push(res.data[item].cname);
                      this.nodeFiledsEn.push(res.data[item].ename);
                  }
              } else {
                  this.nodeFileds = [];
                  this.nodeFiledsEn = [];
              }
          })
      },
    // 获取接口数据
    getInterface(){
      this.$API.getInterface({from:1,size:999}).then(res => {
        if(res.code === 1000){
          this.interfaceData = res.data;
        } else {
          this.interfaceData = [];
        }
      })
    },
    back(){
      history.go(-1)
    }
  },
  created() {
    if(this.$route.params.type === 'edit'){
      this.getFieldByFieldId(this.$route.query.fieldId);
    }
    this.getInterface();
    this.getDicData();
  },
};
</script>
<style>
.field-opt .ant-form-item {
  min-height: 60px;
}
.field-opt .action button {
  margin: 10px 10px;
  width: 120px;
}
.field-opt .pageContent {
 overflow-x: hidden
}
</style>
